import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { findPostById } from 'services/api';

// !==============================DAL (Data Accsess Layer)=================
//   ,      ' :
// 1)    ,   THUNK    
// 2)    'async () => {}',      (data)  thunkApi

export const requestPosts = createAsyncThunk(
  'posts/get',

  async (query, thunkApi) => {
    try {
      //   
      const searchedPosts = await findPostById(query);
      return searchedPosts; //   action.payload
    } catch (error) {
      //      thunkApi     rejectWithVale   ' 
      return thunkApi.rejectWithValue(error.message);
      //   rejected -       
    }
  }
);

// !========================End/ DAL =================================

//    state
const INITIAL_STATE = {
  posts: null,
  isLoading: false,
  error: null,
};

// Slice -  
const postsSlice = createSlice({
  //  ' 
  name: 'posts',
  //    ' 
  initialState: INITIAL_STATE,
  //  ' '
  // !===========================REDUX BLL===========================
  // thunk   '   builder.addCase   thank
  extraReducers: builder =>
    builder
      // PENDING -   
      .addCase(requestPosts.pending, (state, action) => {
        state.isLoading = true;
        state.error = null;
      })
      //   FULFILED -   ,   
      .addCase(requestPosts.fulfilled, (state, action) => {
        state.isLoading = false;
        //  Reducer       -- action.payload
        state.posts = [action.payload];
        // [action.payload] =       searchedPosts    , - api,  '
      })
      // REJECTED -     
      .addCase(requestPosts.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      }),
  //   !=====================End/ REDUX BLL===========================
});

// ' 
export const postsReducer = postsSlice.reducer;
